<template>
	<div class='test'>
		<p>{{title}}</p>  <!--{{}}-显示JS里的数据-->
    <p v-html='title' 
      :style="{'margin-left':dbCurNum}"></p>
    <h3 v-text='title'></h3>
    <input type="text" 
      v-model='title'
     @keydown.enter='curIndex=2'>
    <input type="checkbox" 
      v-model='checked'
      >
    {{checked}}
    <p 
      v-for='(value,index) in nav'
      v-bind:title='value' 
      :key='value'
      :class='{ on: index == curIndex }'
      @click='show(index)'> 
      <!--key用来区分数据-->     <!--v-bind的缩写就是:-->
     {{index}}--{{ value }}
    </p>
    <button @click='isShow=!isShow'>toggle</button>
    <p 
       v-if='isShow'>   <!-- if和show效果一样。性能不同 --> 
      show
    </p>
    <p v-else='isShow'>
      else
    </p>
    {{ dbCurNum }}
	</div>
</template>
<script>
export default{
	data(){
    return {
      title:'<h1>我好帅</h1>',
      checked:false,
      nav:['首页','产品','我们'],
      isShow:false,
      curIndex:0
    }
  },
  methods:{
    show(index){
      this.curIndex=index;
    }
  },
  computed:{
    dbCurNum(){
      return this.curIndex*20+'%'
    }
  }
  // components:{
  //   hello
  // }
}

</script>
<style scoped>   /* scoped让你的style只作用当前页面*/
.on{
  font-size:50px;
}
</style>


